<template>
  <div class="choice-table">
      <el-table :data="questions.items" >
        <el-table-column
        width="120px"
          label="试题编号">
          <template slot-scope="scope">
            {{scope.row.number}}
          </template>
        </el-table-column>
        <el-table-column
        width="120px"
          label="学科">
          <template slot-scope="scope">
            {{scope.row.subject}}
          </template>
        </el-table-column>
        <el-table-column
        width="120px"
          label="目录">
          <template slot-scope="scope">
            {{scope.row.catalog}}
          </template>
        </el-table-column>
        <el-table-column
          label="题型">
          <template slot-scope="scope">
            {{scope.row.questionType === 1 ? '单选' : '多选'}}
          </template>
        </el-table-column>
        <el-table-column
        width="280px"
          label="题干">
          <template slot-scope="scope">
            <div v-html="scope.row.question "></div>
          </template>
        </el-table-column>
        <el-table-column
        width="180px"
          label="录入时间">
          <template slot-scope="scope">
            {{scope.row.addDate | parseTimeByString }}
          </template>
        </el-table-column>
        <el-table-column
          label="难度">
          <template slot-scope="scope">
            {{scope.row.difficulty === '1' ? '简单' : (scope.row.difficulty === '2' ? '一般' : '困难')}}
          </template>
        </el-table-column>
        <el-table-column
          label="录入人">
          <template slot-scope="scope">
            {{scope.row.creator}}
          </template>
        </el-table-column>
        <el-table-column
          label="审核状态">
          <template slot-scope="scope">
             {{ scope.row.chkState ? '已审核' : '待审核' }}
          </template>
        </el-table-column>
        <el-table-column
          label="审核意见">
          <template slot-scope="scope">
            {{scope.row.chkRemarks}}
          </template>
        </el-table-column>
        <el-table-column
          label="审核人">
          <template slot-scope="scope">
            {{scope.row.chkUser}}
          </template>
        </el-table-column>
        <el-table-column
          label="发布状态">
          <template slot-scope="scope">
            {{scope.row.publishState ? '已发布' : '待发布'}}
          </template>
        </el-table-column>
        <el-table-column
          width="200px"
          fixed="right"
          label="操作">
          <template slot-scope="scope">
            <el-button style="font-size: 12px;" type="text" @click="preview(scope.row.id)">预览</el-button>
            <el-button type="text" :disabled="scope.row.chkState ? true : false" @click="audit(scope.row.id)">审核</el-button>
            <el-button type="text" @click="$router.push(`new?id=${scope.row.id}`)" :disabled="scope.row.publishState ? true : false">修改</el-button>
            <el-button type="text" @click="goUp(scope.row.id,scope.row.publishState)">{{scope.row.publishState?'下架':'上架'}}</el-button>
            <el-button type="text" @click="del(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
       <el-dialog  title="题目审核" :visible.sync="showAudit" width="400px">
            <el-form :model="dialogForm" style="margin-top: 0;">
              <el-form-item >
                 <el-radio v-model="radio" label="1">通过</el-radio>
                  <el-radio v-model="radio" label="2">拒绝</el-radio>
              </el-form-item>
              <el-form-item>
                <el-input
                    type="textarea"
                    autosize
                    placeholder="请输入内容"
                    v-model="chkRemarks">
                  </el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer" style="text-align: right;">
              <el-button @click="showAudit = false">取 消</el-button>
              <el-button type="primary" @click="submitAudit" >确 定</el-button>
            </div>
          </el-dialog>
          <!-- 预览功能：点击编号打开题目详情对话框 -->
        <QuestionsPreview></QuestionsPreview>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex'
import QuestionsPreview from './questions-preview.vue'
// import { detail } from '../../api/hmmm/questions'
export default {
  components: {
    QuestionsPreview
  },
  name: 'choiceTable',
  // props: {
  //   questions: Array
  // },
  data () {
    return {
      showAudit: false,
      radio: '1',
      chkRemarks: '',
      auditId: '',
      showPreview: false,
      videoShow: false,
      dialogForm: {}
    }
  },
  methods: {
    ...mapActions('questionsChoice/article', ['choiceCheckAsync', 'choicePublishAsync', 'removeAsync', 'getChoiceAsync']),
    ...mapActions('questionsChoice/questionsRandoms', ['lookDetailAsync']),
    ...mapMutations('questionsChoice/questionsRandoms', ['setShowPreview']),
    async preview (id) {
      console.log(id)
      // this.showPreview = true
      this.setShowPreview(true)
      // this.$emit('preview', id)
      // const res = await detail({ id })
      // console.log(res.data)
      // this.randomsDetail = res.data
      this.lookDetailAsync({ id })
    },
    audit (id) {
      console.log(id)
      this.showAudit = true
      this.auditId = id
      this.chkRemarks = ''
      this.radio = '1'
    },
    async submitAudit () {
      // this.choiceCheckAsync({
      //   id: this.auditId,
      //   chkState: this.radio,
      //   chkRemarks: this.chkRemarks
      // })
      if (this.chkRemarks === '') {
        this.$message({
          message: '请输入审核意见',
          type: 'warning'
        })
        return
      }
      this.choiceCheckAsync({
        id: this.auditId,
        chkState: +this.radio,
        chkRemarks: this.chkRemarks
      })
      // const res = await choiceCheck({
      //   id: this.auditId,
      //   chkState: +this.radio,
      //   chkRemarks: this.chkRemarks
      // })
      // console.log(res, 1111)
      this.showAudit = false
      // const res1 = await choice({ pagesize: 5 })
      // this.getChoiceAsync()

      this.$message({
        message: '审核成功',
        type: 'success'
      })
    },
    goUp (id, state) {
      console.log('上架')
      this.$confirm(`您确认${state ? '下架' : '上架'}这道题目吗?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        console.log(id, state)
        await this.choicePublishAsync({ id, publishState: (state === 1 ? 0 : 1) })
        // this.getChoiceAsync({ pagesize: 5 })
        this.$emit('setQuestionsArr')
        this.$message({
          type: 'success',
          message: `${state ? '下架' : '上架'}成功!`
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        })
      })
    },
    del (id) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        await this.removeAsync({ id })
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    closeDialog () {
      this.showPreview = false
      this.videoShow = false
    },
    setQuestions (obj) {
      console.log(88888888)
      this.questions = obj
    }
  },
  async created () {
    // await this.getQuestionsAsync()
    // console.log(this.getSubject.question.items)
    // this.questions = this.getSubject.questions.items
    // console.log(this.questions)
  },
  computed: {
    ...mapState('questionsChoice/article', ['questions'])
    // ...mapState('questionsChoice/questionsRandoms', ['randomsDetail'])

  }
}
</script>

<style lang="scss" scoped>
    ::v-deep .el-button--text {
        span {
            font-size: 12px;
        }
    }
    ::v-deep tbody {
      width: 500px;
    }

// ::v-deep .el-table{
//   width: 100% !important;
// }
::v-deep .el-dialog__body{
  .info{
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    // flex: 4;
    div{
      flex: 0 0 25%;
      padding: 10px 0;
    }
  }
}
::v-deep .dialog-footer{
  display: flex;
  justify-content: flex-end;
}
::v-deep .toggle-content{
  color: blue;
}
::v-deep .answerHtml{
  display: flex;
  align-items: center;
}
.video{
  video{
    width: 400px;
    height: 300px;
  }
}
.notice{
  margin-bottom: 13px;
}
::v-deep .el-radio{
  margin-bottom: 13px;
}
</style>
